.gallery-container {
    text-align: center;
    padding: 40px 20px;
    background-color: #0a0a23;
    color: white;
  }
  
  .gallery-title {
    font-size: 2.2rem;
    color: #00aaff;
    margin-bottom: 10px;
  }
  
  .gallery-subtitle {
    font-size: 1.2rem;
    color: #cccccc;
    margin-bottom: 20px;
  }
  
  .image-slider {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 20px 0;
  }
  
  .gallery-image {
    width: 400px; /* Reduced from 500px */
    height: auto;
    border-radius: 8px;
    box-shadow: 0 0 15px rgba(0, 170, 255, 0.5);
  }
  
  .nav-button {
    background: none;
    border: none;
    font-size: 2rem;
    color: white;
    cursor: pointer;
    margin: 0 20px;
    transition: 0.3s;
  }
  
  .nav-button:hover {
    color: #00aaff;
  }
  
  .image-thumbnails {
    display: flex;
    justify-content: center;
    margin-top: 15px;
  }
  
  .thumbnail {
    width: 60px; /* Reduced from 80px */
    height: auto;
    margin: 5px;
    cursor: pointer;
    border-radius: 5px;
    transition: transform 0.2s, opacity 0.3s;
    opacity: 0.6;
  }
  
  .thumbnail.active {
    transform: scale(1.1);
    opacity: 1;
    border: 2px solid #00aaff;
  }
  
  .thumbnail:hover {
    transform: scale(1.05);
    opacity: 0.8;
  }
  